import { useNavigate } from 'react-router';
import '../App.css';
import Steam from '../images/Group.png'

export default function Header({logo, login, logout, isLogin, response}) 
{ 
  let route = useNavigate();
   return (
    <>
      <div className="header">
        <img className="logo" style={{cursor: 'pointer'}} onClick={()=>route('/')} src={logo} alt="description"/>
        <div className="menu">
          {isLogin && <span className="text" onClick={()=>route('/account/')}>Личный кабинет</span>}
          <span className="text" onClick={()=>route('/comment/')}>Отзывы</span>
          <span className="text" onClick={()=>route('/help/')}>Помощь</span>
          {isLogin && <span className="text" onClick={()=>route('/market/')}>Продать предметы</span>}
        </div>
        <div className="auth">
          <img alt="description" src={response?.extra_data?.avatar || Steam} className="login-btn"/>
          {!isLogin && <span className="text" onClick={login}>Войти через Steam</span>}
          {isLogin && <span className="nickname">{response?.extra_data?.personaname}</span>}
          {isLogin && <span className="text" onClick={logout}>Выйти</span>}
        </div>
      </div>
    </>
  );
}