import React, { useState } from 'react';
import Button from '@mui/material/Button';
import axios from 'axios';

function Helpmodal({response, base_url}) {
        const [text_message, setHelpText]  = useState('')
        const [email, setEmail]  = useState('')

        function createComm(){
                axios.post(`${base_url}api/send-tech-help/`, {text_message, email, id:response?.id})
                setHelpText('');
        }
    return (
        <>
                <div className='feedback-modal'>
                    <div className='feedback-modal-header'>
                        <p className='text-feedback'>Отзыв</p>
                    </div>
            
                <div className='feedback-info'>
                    <img alt="avatar" src={response?.extra_data?.avatar } className="login-btn"/>
                    <div className='text-feedback'>Вы вошли как {response?.extra_data?.personaname}</div>
                </div>
                <div className='feedback-form'>
                    <textarea className='textarea' placeholder='Текст' cols="30" rows="10" maxlength="255" onChange={(e)=>setHelpText(e.target.value)}></textarea>
                    <textarea className='textarea' placeholder='email' cols="30" rows="10" maxlength="255" onChange={(e)=>setEmail(e.target.value)}></textarea>
                    <div className='feedback-button'>
                        <Button variant="contained" color="success" onClick={createComm}>Отправить</Button>
                    </div>
                    </div>
                </div>
    </>
    );
  }

  export default Helpmodal;