import 
{ 
    useCallback, 
    useEffect, 
    useState, 
    memo, 
    useMemo 
} from 'react';
import { Navigate  } from 'react-router-dom'
import axios from 'axios';
import Item from '../components/item'; 

function Market({default_link_market, logged_in_market}) 
{ 
    const inventory = useMemo(() => localStorage.getItem('inventory'), [])
    const [sortedItems, setSortedItems] = useState([])
    const [response, setresponse] = useState()
    const [link, setlink] = useState()
    const [credentials, setCredentials] = useState()
    const choosedItems = useMemo(() => sortedItems.filter(item=>item.checked), [sortedItems])
    const items = []
    const template_items = []
    const totalPrice = useMemo(() => {
        let price = 0;
        choosedItems.forEach(item=>price+=+item.price)
        return price.toFixed(2);
    },[choosedItems])
    const getInventory = useCallback(async () => 
    {
        await axios.get(`${default_link_market}api/inventory/`, {withCredentials: true}).then((r) => 
        { 
            if (Object.keys(r.data).length !== 0) 
            {
                localStorage.setItem('inventory', JSON.stringify(r));
                setresponse(r.data);
            }
        });
      },
      [default_link_market],
    )
    useEffect(() => 
    {
        if (inventory) 
        { 
            setresponse(JSON.parse(inventory)?.data)
        }
        else 
        {
            getInventory();
            return;
        }
        if (Object.keys(JSON.parse(inventory).data).length === 0) 
        {
            getInventory();
        }
    }, [getInventory, inventory])
 
    useEffect(() => 
    {
        for (let item in response?.rgDescriptions) 
        {
            template_items.push(response.rgDescriptions[item]);
        }
        
        for (let item in response?.rgInventory) 
        {
            items.push(response.rgInventory[item]);
        }
         const temp_arr = (items.map((item) => {
            for (let i = 0; i < template_items.length; i++) 
            {
                if (item.instanceid === template_items[i].instanceid && item.classid === template_items[i].classid) 
                {
                    if (template_items[i].tradable && template_items[i].marketable) 
                    { 
                        template_items[i].checked = false
                        return {...template_items[i]}
                    }
                }
            }
            return null
        })).filter(item => item != null)
        let flag = 0;
        temp_arr.forEach(item=> {
            item.id = flag;
            flag+=1;
        })
        setSortedItems(temp_arr);
    }, [response])
    console.log(sortedItems);
    const chooseItem = (id) => {
        sortedItems.find((item, itemId)=>{
            if(item.id == id)
            {
                const temp_arr = [...sortedItems]
                temp_arr[itemId].checked = !temp_arr[itemId].checked;
                console.log(itemId);
                setSortedItems(temp_arr);
                return null;
            }
        })
    }

    function getMoney () 
    {
        axios.post(`${default_link_market}api/applications/`, {hash_names:choosedItems.map(item=>item.market_hash_name), summary:totalPrice, credentials}, {withCredentials: true})
    }

    function savelink()
    {
        axios.post(`${default_link_market}api/add-link/`, {link:link}, {withCredentials: true})
    }
  return (
    <div className="sell-page">
        {!logged_in_market && <Navigate replace to={'/'} />}
         <div className="modal-items">
            <div className="modal-header">
               <img className="steam-icon" alt='dsdd' src="https://avan.market/templates/frontend/default/images/steam-icon-skins.svg"/>
                <span className="text-with-icon"><b>Ваш</b> инвентарь</span>
            </div>
            <div className="link">
                    <span className="input-your-link">Вставьте свою ссылку на обмен:</span>
                    
                    <input type="text" onChange={(e)=>setlink(e.target.value)} className="input-link" placeholder="Ссылка на обмен"/>
                    <button className="save-offer-link" onClick={savelink}>СОХРАНИТЬ</button>
                </div>
                <div className="items">
                    {sortedItems.map(item => <Item chooseItem={chooseItem} key={Math.random()} item={item}/>)}
                </div>
                <div className='selected'>Выбрано: {choosedItems.length}
                <button className='reload-inventory' onClick={getInventory}>Обновить</button></div>
                
         </div>

         <div className="modal-items">
            <div className="modal-header">
                <img className="steam-icon" alt='sdsds' src="https://avan.market/templates/frontend/default/images/steam-icon-skins.svg"/>
                <span className="text-with-icon"><b>Вы</b> получите</span>
            </div>
            <div className='amount'>
                <span className='trade-amount'>
                    <span className='dash'></span>
                    <span className='cost'>{totalPrice}</span>
                    <span className='cur'>₽</span>
                </span>
            </div>
            
            <div className='method'>
               <div className='name'>Выбрать способ оплаты</div>
               <div className='payment-list'>
                     <div className='payment visa'></div>
                     <div className='payment qiwi'></div>
               </div>
            </div>
            <div className='fields'>
                <div className='text-total-desc'>Платежные реквизиты</div>
                <input type="text" className="email" placeholder="Реквизиты" onChange={(e)=>setCredentials(e.target.value)}/>
            </div>
            <button className="get-money" onClick={getMoney}>ПОЛУЧИТЬ ДЕНЬГИ</button>
         </div>
    </div>
    
  );
}

export default memo(Market);