import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { Navigate } from 'react-router-dom';
import '../App.css';
import Row from '../components/row';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableContainer from '@mui/material/TableContainer';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Paper from '@mui/material/Paper';

export default function Account({default_link_lk, logged_in_lk, account}) 
{ 
  const [state_item, setState_item] = useState([])
  useEffect(() => { getState_items() }, [])
  const getState_items = async()=>
  {
    const state_items = await axios.get(`${default_link_lk}api/applications/`, {withCredentials: true}) 
    setState_item(state_items.data);
  }
  
  return (
    <>
    {!logged_in_lk && <Navigate replace to={'/'}/>}
    <div className='cabinet-header'>
      <h1>ЛИЧНЫЙ КАБИНЕТ</h1>
    </div>
    <TableContainer sx=
    {{width:'80%', maxHeight:700, margin: 'auto', background:'#2A2833'}} component={Paper}>
      <Table size='medium' aria-label="collapsible table">
        <TableHead>
          <TableRow >
            <TableCell />
            <TableCell id='table-items'>Тип</TableCell>
            <TableCell id='table-items' align="left">Дата</TableCell>
            <TableCell id='table-items' align="right">Сумма</TableCell>
            <TableCell id='table-items' align="right">Реквизиты</TableCell>
            <TableCell id='table-items' align="right">Статус</TableCell>
            {account.is_admin && <TableCell id='table-items' align="right">Действие</TableCell>}
          </TableRow>
        </TableHead>
        <TableBody>
          {state_item.map((item) => (<Row key={item.id} item={item} user={account} base_url={default_link_lk}/>))}
        </TableBody>
      </Table>
    </TableContainer>
    </>
  );
}