import './App.css';
import axios from 'axios';
import { 
  useEffect, 
  useState 
} from 'react';
import logo from './images/logo.png'
import Header from './components/header';
import Routes from './Routes';

const public_url =   'http://localhost:8000/'
//const public_url = 'https://sellobjects.herokuapp.com/'

export default function App() 
{ 
  const [response, setResponse] = useState({});
  const [is_auth, setAuth] = useState(false);

  function login() 
  {
    window.location.href = `${public_url}accounts/steam/login/`
  }

  function logout() 
  {
    axios.get(`${public_url}accounts/logout/`, {withCredentials: true}).then(() => {
      setAuth(false);
      setResponse({});
      localStorage.clear();
    }).catch(() => {
      setAuth(false);
      setResponse({});
    })
  }

  useEffect(() =>{
    getInfo();
  },[])

    function getInfo() 
    { 
      axios.get(`${public_url}api/me/`, {withCredentials: true}).then((resp) => {
        setResponse(resp.data);
        setAuth(true);
    }).catch(() => {
      setAuth(false)})
   }

  return (
    <>
      <Header logo={logo} login={login} logout={logout} isLogin={is_auth} response={response}/>
      <Routes default_link={public_url} logged_in={is_auth} response={response}/>
    </>
  );
}